@import '../../../scss/button';

%modal-body {
   .contact__form__container {
	   font-size: 1rem;
	   margin: 0 1.5rem;
	   form {
		   @extend %flex;
		   @extend %flex-center;
		   div {
			  margin: 0.25rem 0;
			  label {
				  font-size: 1.5rem;
				  margin: 1rem 0;
			  }
			  input[type=text] {
				  font-size: 1.25rem;
				  margin: .5rem 0;
				  padding: .25rem 1rem;
				  border: 0;
				  outline: 0;
				  background: transparent;
				  border-bottom: 1px solid black;
				  color: black;
				  &::placeholder {
					  color: black;
				  }
			  }
			  &.error {
				   color: red;
				   input {
					  color: red;
					  margin-bottom: 0.5rem;
					  border-bottom-color: red;
					  &::placeholder {
						   color: red;
					  }
				   }
				   span {
					   font-style: italic;
				   }
			  }
		   }
		   .btn {
				@include button(1.5rem, .75rem 1.25rem, .75rem 0);
				&:active {
					transform: scale(0.98);
				}
		   }
	   }
   }
}
