@import '../../scss/config';

.projects {
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: center; 
   #projects-title {
		margin: 0 10vw;
		padding: 10vh 0 0 0;
		font-size: 1.5rem; 
		text-decoration: underline;
   }
   #projects-body {
		margin: 10vh 10vw;
	  	.project {
		   padding: 5vmin 5vmin 10vmin 10vmin;
		   margin: 0 5vmin 5vmin 5vmin;
		   &:nth-child(2n) {
			  padding-top: 5vmin;
		   }
		   &:not(:last-child) {
			  border-bottom: 2px solid set-text-color($primary-color);
		   }
		   &-header {
			    display: flex;
			  	.icon {
				   font-size: 5.5rem;
				}
				.project-title {
				   	align-self: center;
					text-align: right;
				}
		   }
		   &-body {
				display: flex;
				flex-direction: column;
				&__desc {
				   margin: 2vmin 0;
				}
				&__links {
				   margin-top: 3vmin;
				   display: flex;
				   justify-content: space-around;				   
				   span {
					  border: 2px solid set-text-color($primary-color);
					  padding: 0.75rem;
				   }
				}
		   }
		   &:hover {
			  background: lighten($primary-color, 10);
			  border-radius: 1.5rem;
			  border: none;
			  @include box-shadow(5px, 3px, 25px, 15px, rgb(0 0 0 / 0.2), false);
		   }
		}
   }
}
