@import '../../scss/config';

.home {
   	height: 100%;
	padding: 40vh 5vw 0 5vw;
	overflow: hidden;
	&__name {
	   font-size: 5rem;
	   padding-bottom: 1rem;
	   margin-bottom: 1rem;
	   border-bottom: 2px solid set-text-color($primary-color);
	   &__last {
		  color: $secondary-color;
		  font-weight: 700;
		  &:before {
			 content: ' ';
		  }
	   }
	}
}
