@use 'sass:math';
@import 'modal-body', '../../../scss/ellipsis';

@mixin modal($width, $height: -1, $bg-color, $text-color, $close-right:false, $centered: true) {
	 position: absolute;
	 @if($centered) {
		top: 50% - math.div($width, 2);
		left: 50% - math.div($height, 2);
	 }
	 width: $width;
	 background: $bg-color;
	 color: $text-color;
	 @extend %flex;
	 .modal-header {
		font-size: 2rem;
		@extend %flex;
		&-close {
		   @if($close-right) {
			  margin-right: 0.5rem;
			  align-self: flex-end;
		   }
		   margin-left: 0.5rem;
		   cursor: pointer;
		}
		&-text {
		   margin-left: 1.5rem;
		   @extend %ellipsis;
		}
	 }
	 .modal-body {
		margin: 1rem;
		height: 100%;
		@extend %modal-body;
	 }
}
