@import '../../scss/config';

footer {
    display: block;
    width: 100vw;
    position: fixed;
	bottom: 0;
	padding-top: .5rem;
    background: lighten($primary-color, 10);
    .footer-content {
         display: flex;
		 justify-content: space-around;
		 align-items: center;
		 &__social-icons {
			font-size: 2.25rem;
		 	&__email {
			   @include transition-ease;
			   &:hover {
				  color: $secondary-color;
				  cursor: pointer;
			   }
			}
			&__link {
			   @include transition-ease;
			   padding: .35rem;
			   &:hover {
				  color: $secondary-color;
			   }
			}
		 }
		 &__signature {
			font-size: 1.15rem;
		 }
    }
}             
