@import '../../scss/config';

.about {
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-bottom: 2rem;
   &__bio {
	  display: flex;
	  flex-direction: column;
	  justify-content: space-evenly;
	  align-items: center;
	  height: 50vh;
	  margin: 0 10vw;
	  padding: 10vh 0 0 0;
	  h2 {
		 text-decoration: underline;
	  }
   }
   #resume-title {
	  margin: 10px 0 18px 25px;
	  font-size: 1.5rem;
	  text-decoration: underline;
   }
   #res-obj {
	  margin: 0 3.75rem;
	  font-size: .85rem;
	   #obj-title:after {
		   content: ':';
		   margin-right: 5px;
	   }
   }
   #res-skills {
	   margin: 2.75rem;
	   #skills-title:after {
		   content: ':';
	   }
	   ul {
		   margin: 10px 15px;
		   #res-skills-list-title {
			   text-decoration: underline;
			   line-height: 1.5rem;
		   }
		   li {
			   margin: 0 20px;
		   }
	   }	
   }
   #res-projects {
	   margin: 0 2.75rem;
	   #res-projects-title:after {
		   content: ':';
	   }
	   ul {
		   margin: 10px 15px;
		   li {
			   margin: 5px 15px;
			   #res-projects-desc-heading {
				   font-weight: 700;
				   border-bottom: 1px solid darken($primary-color, 75);
				   margin: 10px;
			   }
		   }
	   }
   }
   #res-experience {
	   margin: 2.75rem;
	   #res-experience-title:after {
		   content: ':';
	   }
	   ul {
		   margin: 10px 15px;
		   li {
			   margin: 0 15px;
			   #res-exp-heading {
				   font-weight: 700;
				   margin: 10px 0;
			   }
			   #res-exp-company {
				    font-style: italic;
			   		font-size: .85rem;
			   }
			   #res-exp-city {
				  	margin: .65rem 0;
			   		font-size: .85rem;
			   }
			   #res-exp-duration {
				   font-size: .75rem;
				   span:first-child:after {
					   content: ' - ';
				   }
			   }
			   #res-exp-responsibilities {
				   font-size: .75rem;
			   }
		   }
	   }
   }
}
